import * as React from "react";
import PropTypes from "prop-types";
import Progress from "./Progress";
import Header from "./Header";
import Search from "./Search";
import TemplateList from "./TemplateList";
import { useState, useEffect } from "react";

/* global Word, require */

export default function App({ title, isOfficeInitialized }) {
  const [templateType, setTemplateType] = useState("ORG");
  const [search, setSearch] = useState("");
  const [organizationTemplateGroups, setOrganizationTemplateGroups] = useState([]);
  const [skaiTemplateGroups, setSkaiTemplateGroups] = useState([]);

  useEffect(() => {
    fetchOrganizationTemplateGroups(setOrganizationTemplateGroups);
    fetchSkaiTemplateGroups(setSkaiTemplateGroups);
  }, []);

  const fetchTemplateGroups = (url, setTemplateGroups) => {
    fetch(url)
    .then(response => response.json())
    .then(response => {
      console.log(response);
      setTemplateGroups(response);
    })
    .catch(err => {
      console.log(err);
    });
  }

  const fetchOrganizationTemplateGroups = (setTemplateGroups) => {
    fetchTemplateGroups("https://raw.githubusercontent.com/lseangmeng/testing/main/test1.js", setTemplateGroups);
  }

  const fetchSkaiTemplateGroups = (setTemplateGroups) => {
    fetchTemplateGroups("https://raw.githubusercontent.com/lseangmeng/testing/main/test2.js", setTemplateGroups);
  }

  const getTemplateGroups = () => {
    let templateGroups = [];
    if (templateType === "ORG") {
      templateGroups = organizationTemplateGroups;
    } else {
      templateGroups = skaiTemplateGroups;
    }

    if (search !== "") {
      templateGroups = templateGroups.filter(g => g.templates.filter(t => t.content.indexOf(search) !== -1).length > 0);
    }

    return templateGroups;
  };

  const onTemplateOpen = async (template) => {
    return Word.run(async (context) => {
      /**
       * Insert your Word code here
       */

      // insert a paragraph at the end of the document.
      const paragraph = context.document.body.insertParagraph(template.content, Word.InsertLocation.end);

      // change the paragraph color to blue.
      paragraph.font.color = "blue";

      await context.sync();
    });
  };

  if (!isOfficeInitialized) {
    return (
      <Progress
        title={title}
        logo={require("./../../../assets/logo-filled.png")}
        message="Please sideload your addin to see app body."
      />
    );
  }

  return (
    <div>
      <div className="App">
        <Header templateType={templateType} setTemplateType={setTemplateType} />
        <Search onChange={setSearch} />
        <TemplateList templateGroups={getTemplateGroups()} onTemplateOpen={onTemplateOpen} />
      </div>
    </div>
  );
}

App.propTypes = {
  title: PropTypes.string,
  isOfficeInitialized: PropTypes.bool,
};
