import * as React from "react";
import { useState } from "react";

function TemplateList({templateGroups, onTemplateOpen}) {
    return (
        <div>
            { templateGroups.map(g => <TemplatesInGroup key={g.name} group={g} onTemplateOpen={onTemplateOpen} />) }
        </div>
    );
}

function TemplatesInGroup({group, onTemplateOpen}) {
    const [collapse, setCollapse] = useState(group.collapse);
    return (
        <div>
            <div onClick={() => setCollapse(!collapse)} >
                {getCollapseSign(collapse)}  {group.name}
            </div>
            { collapse && group.templates.map(t => <Template key={t.name} template={t} onTemplateOpen={onTemplateOpen}/>) }
        </div>
    );
}

function Template({template, onTemplateOpen}) {
    return (
        <div>&nbsp;{template.name} <span className="Open" onClick={() => onTemplateOpen(template)}>Open</span></div>
    );
}

function getCollapseSign(collapse) {
    if (collapse) return "-";
    else return "+";
}

export default TemplateList;