import * as React from "react";
import TemplateTitle from "./TemplateTitle";

function Header({templateType, setTemplateType}) {
    return (
        <div>
            <TemplateTitle 
                title="Templates" 
                selected={ templateType === "ORG" } 
                onClick={ () => setTemplateType("ORG") }
            />&nbsp;
            <TemplateTitle 
                title="Skai Templates" 
                selected={ templateType === "SKAI" } 
                onClick={ () => setTemplateType("SKAI") }
            />&nbsp;
        </div>
    );
}

export default Header;